export default [{"id":"16","content":"一个杂记本\n\n<strong>-- 读书清单 --</strong>\n\n<i>- 已读 -</i>\n全球通史（上）\nNonviolent Communication\nTo Kill a Mockingbird\nThe hard thing about hard things\n\n<i>- 备选 -</i>\n\b创新者的窘境\n如何阅读一本书\n全球通史（下）\n漫步华尔街\n非理性繁荣\n时间简史\n未来简史\n影响力\n\n<strong>-- blog features --</strong>\n\n<s>quote</s>\n<s>responsive</s>\n<s>css-in-js (w. emotion)</s>\n<s>react fc</s>\n<s>babel instead of ts-loader</s>\n<s>eslint</s>\n<s>js hash</s>\n<s>字数统计</s>\n侧栏\n标签统计\nfavicon\n动态yarn article\nnode in ts\nslide in\n\n","title":"杂记本","time":"top"},{"id":"14","content":"Reading - 2/10\nWorkout - 0.5/5\nWeight - 74.1\n\n这周最终确定了Landing的入职时间，也向Airbnb提了离职。两个月以来压在心中的一块石头终于放下了，有些如释重负。在Airbnb这两个月倒也没有懈怠，也算是全心全意的在工作。只是我想如果我没有知道自己要走的话会去努力承担responsibility，例如\b\b和大家1on1，例如一些long term projects，但是心中总是想着要走了，还是有不少的回避。现在正式提了辞职，这部分压抑也算是释怀了。\n\n想了很久，和爸妈朋友聊了很久，这个决定还理性的吧。我相信Airbnb终究是会活过来的，甚至也许活过来会更好。但是短时间（可能今年）会比较艰难，另外我们也不知道China Team会不会一起活下来，还是成为牺牲的一部分。相对，吴田和何总的话来讲Landing现在资金还是充足的，相比于旅游业To C的直接打击，To B收到的Impact也还是小一些。考虑到宝宝短时间内可能是走不了了，还是应该避免两个鸡蛋都放在一个篮子的风险吧。\n\n从领域公司挑战成长来看，当然选Landing没问题。但一个人有时差的remote工作，这里面的挑战也不小，想想也许之后都需要早上7点起床工作然后一个人WFH一整天（尤其可能其他人都复工回办公室工作了），是有点孤单呢。不过既然做出了选择，这些挑战也只能去克服了，也许体验下来可能还不错呢？\n\nLanding就聊到这里吧。\n\n我要开始反思自己工作之余的生活了，进入3月份以来，\b\b二月份保持的阅读健身习惯就慢慢荒废了。读书直接荒废了好几周，运动也慢慢坚持不了每周5次了。\b\b想想慢慢又进入了一种白天工作到6，7点。晚上划水玩动森和德扑。工作忙起来确实可以理解，但是动森和德扑需要做一个选择了。每天2个小时德扑又2个小时动森确实受不了。\n\n下周开始不玩德扑吧，一方面动森毕竟是和宝宝一起玩的，难得有一个游戏我们可以一起玩的这么投入，而且还是不少的社交成分。另外德扑最近有些玩的不太爽，大家总是无脑的allin之后没有玩博弈的乐趣，另外整体上我的运气比较差，领先的牌flop之后被打败的游戏体验太差了。另外总有人游戏时候干别的拖慢游戏节奏，也降级游戏体验。玩了两个月了，也该歇歇了。\n\n除了德扑以外，我也要组织自己看德扑视频和读德扑的书了哈哈，不然会引得手痒痒还是要去玩。这两周尝试读了Modern Poker Theory，不是很好读，有点深。另外里面的策略也真的不适用我们的水平哈哈哈，很多range的判断对于我们这样新手还是不太适用。我要弃坑了，重新读吧。\n\n也是很久不坚持运动了，昨天运动了一半就觉得累的不行了，我觉得可能也需要再次降低一点难度先重新保持习惯最重要。\n\n下周，争取完成Reading和Workout！\n","title":"Weekly - 20.14","time":"2020.04.05"},{"id":"13","content":"Reading - 1/10\nWorkout - 2/5\nWeight - 73.5\n\n今天是H1b抽签结果最后一天，不出意外的话，今年还是没有中签，所以尴尬的记录延长成了四抽不中。当6点钟醒来没有看到landing的讯息，想着梦中好像收到了好消息，边辗转反侧再也睡不着了。\b刷着一亩三分地，看着一个一个高兴雀跃的帖子中家在一些愤怒的帖子。有人三刷四刷中了觉得人间值得，有人四刷五刷没有中，感慨命运不公。\n\n我想我曾经面对抽签也是如此的热忱吧。一次一次的不中，我或多或少都在觉得命运对自己是不公平的。为什么别的人可以早早就解决身份问题，买房换工作自由自在，走在完美人生的路上，而我不得不颠沛流离，失意回国。回国初期也确实很多不顺，压抑了一阵子。\n\n不过回国久了我反而有些感激这一段经历，回国让我体验到了不一样的生活，有了不一样的经理，学习到了不一样的东西。如果我早早就抽中了H1b，我想我是不会有这么一段经历，生活会波澜不惊，也许也是一种失去。\n\n这么说倒不是在说因祸得福，波澜不惊也有波澜不惊的好，\b不过是另外一种选择。想说的只是是上帝为你关上一扇门，还会打开一扇窗。生活中有很多事情是你无法控制的，但没有控制的结果没有好坏。\b\n\n所以四抽不中，固然有些难过（\b不然从一开始也不会想去抽），不过我也能平静的面对，与其怨念，不如想想生活又给我带来了什么不一样的选择。\n\n所以回到landing的问题，我可以选择加入，虽然不能很快回到美国，需要remote工作很久很久，但也许也是一种新的挑战。\u001c我还是选择留在airbnb，去更好的学习成为一个lead/manager的职能，enable一个team或者更多的人做好一些工作。\n","title":"Weekly - 20.13","time":"2020.04.01"},{"id":"12","content":"Reading - 0/10\nWorkout - 2.5/5\nWeight - 73.3\n\n连续两周读书弃疗中，这周运动也没有做到，\b\b上周日熬夜太晚直接导致周一周二工作没有精力，\b周中的运动被放弃，直到周末才回复两次。\n\n上周日为了写日记熬到2点半，最后日记没写成，还周一完全没办法工作，真是一个教训。规律的作息还是很重要的，之后还是要确保1点之前能睡觉，然后9点起床。这样才能确保第二天的精神。\b\b此时此刻我就在警示自己1点之前一定要写完这篇文章。\n\n--\n\n回到公园五号之后就发现周末回到了之前的状态，无心读书，无心加班，时间都沉沦在游戏中。每到周末之前我都会告诉自己周末要做一些正经事情，但最后turns out都还是荒废了。我决定要删掉手机里的小游戏了，晚上的时候可以玩德扑做消遣，至少德扑可以让我学习情绪管理，而且也要求有人一起。\n\n说道德扑，最近情绪管理比之前好多了，也慢慢赢得多输的少了。虽然时不时还是会出现不够冷静，冲动的情况，但是整体上对自己打牌的风格有了比较多了解。\b知道自己在赢牌之后也容易松懈，最适合的打牌时间是一个小时，如果这个时候如果是大赢，最好赶紧止盈，不然很容易走下坡路。我觉得打德扑还是让我学会了更多的情绪管理。虽然过程中输了一些钱，但是暴露了很多自己的负面情绪，花这些钱也算是值得吧。\n\n--\n\n慢慢的在WFH的日子里，找到了比较好的工作节奏，做Team的Tech Lead也慢慢\b变得顺手，大家也越来越被大家接受。如果考虑回美国，Landing的机会，真的觉得现在做的很好，慢慢成长为一个Tech Lead，一个EM，有这样的机会真的很不容易。\b\b有的时候想如果想着一个月以后我可能就不在这里了，我觉得我可以比现在更努力很多，做得更好。不过也许这只是借口吧，惰性是人类的天性，想想我从大学以来就说周末要学习要加班，似乎也从来没有实现过。也许这也是我要接受自己的一部分吧。\n\n嗯，习惯了WFH的日子，感觉也没有太多变化，能写的也不多，写来写去也就那么一些事情，感觉有些枯燥，希望一疫情赶紧过去，可以早日回到办公室，早日决定我之后在哪里。最后，希望自己后面的日子里，多学习学习赚钱，多看看股票，少玩游戏，多看书。\n","title":"Weekly - 20.11","time":"2020.03.15"},{"id":"11","content":"Reading - 0/10\nWorkout - 5/5\nWeight - 74.3\n\n好吧，如果上周读书是一个弃疗，那这周就完全没有读书了哈哈...\n","title":"Weekly - 20.10","time":"2020.03.08"},{"id":"10","content":"Reading - 1.5/10\nWorkout - 5/5\nWeight - 74.3\n\n这周读书完全是弃疗的状态哈哈。一方面是保持运动看书还是一件挺难得事情。之前在爸妈家里，因为对于德扑比较克制，外加做饭家务都不需要我操心，所以相对时间比较多一些。\b另一方面，The hard thing about hard things的读后感有点难写，感觉记流水账一样的读书感收获有点低，所以不想接着写，索性周六决定还是不要再见是上面卡着自己了，不想写就不写了。现在开始了新书 <The Innovator's Dilemma> 应该可以好一些吧。\n\n不过另一方面，先把运动回归了正规，完成了周定目标5次，\b\b继续加油！\n\n另外决定再把体重记录加回来，这件事困扰我很久因为我总是有点矫情的想每周选一个体重最轻的时间点做体重衡量。总是幻想自己体重是有下降的，这周开始我们还是直面这个数字吧，就是写周记时的体重，不要自欺欺人，也没人看这个数字，好吧。\n\n--\n\n现在已经是疫情在家办公的第四周了。前两周在爸妈家，后面两周在公园五号。今天算是完成了这四周最久出门的一次，去朝阳公园遛了一个小时的弯，走到最后都挺累的了。感觉一两周还行，一个月以后还是有点憋不住想出去玩相见人。\n\n大约有一天在知乎上面看到一个帖子，说疫情之后会不会离婚分手率飙升，没仔细看回答，但\b\b当时觉得这还个问题还挺有意思。想想好像还是有一点道理的，至少两个人过自由2个人24小时的日子容易问题吧。有时候我会觉得满愧疚的，觉得在买菜做饭方面做得没有小宝宝多，她身体不舒服我也不够体贴。还是有些懒散习惯，不是一个居家的人。有时候我也会觉得不满，宝宝对于疫情的担心有些过度，完全不愿意出门，连同一个走廊的lawrence家都不愿意见，我觉得有点夸张了。我也觉得整天总是在家里躺在床上对身体不好，总应该多活动活动的。\n\n不过整体上来讲我觉得还是很好的，嗯，至少我觉得能过程这样我觉得还算蛮幸福的，希望宝宝也是这么觉得吧。\n\n--\n\n好，最后来讲讲\b最近德扑的事情。最近德扑总是在输钱，而且是一输就停不住的那种，经常一输就穿透1000块。\n\n虽然我觉得怪运气都是借口，但我实在有点觉得很多时候运气太不好了，翻牌前领先被翻盘的情况实在有点多。不过即使我觉得自己运气太差，我也必须承认很多局是自己不冷静，实在不擅长打逆风局，非常容易一时运气不好就急躁，太想翻盘，只会导致输的越来越多。可能原本输200，300的局最后因为心态不好最后就输了1000。\n\n逆境下沉住气是一个我知道我真的做的很糟糕的地方，不仅仅是德扑，下象棋也是，生活工作也是。主要是不如意，我往往很难去坚持做好这件事，往往更多是选择长痛不如短痛，急躁的想要赢回来，最后输的更快。或者就是逃避，选择别的出路。如果读The hard thing about hard things 有一点收获的话那还是那句话 If you don't like choosing between horrible and cataclysmic, don't become CEO。可能这是我嘴不适合做CEO的地方，沉不住气，容易急躁，虽然说过一百遍了。但是还是做不好。\n\n也许我需要给自己定义某种提示某种行为，可以让自己在这种沉不住气的时候做一些什么让自己冷静一下。之后可以尝试一下小小的自残？做一组运动？找个冰块让自己冷静一下？\n\n另外我也需要去接受这个世界上不是什么都会赢的，承认自己的上限，接受自己的不足。例如德扑就是会输钱，可能你玩的没有别人好，重要的不应该是你到底赢没赢钱，而是你从里面活得了其它什么，友谊也好，风险承担也好，或者如果能帮助自己在逆境中做的更好。这些可能都比几百块钱的输赢更重要吧。\n\n学会忍受失败。德扑输钱不要急着赚回来，不要改变自己的原则。\b看球落后的时候不要不看了，坚持看完。\n\n学会\b从失败中学习。德扑赢钱输钱都应该回看自己的局，不要只有赢钱心情好才回看，输了就不想看了，逼自己去接受失败并从中间学习。\n\n--\n\n顺便给自己一周冷静的时间吧，周中不能玩德扑了，可以顺便捡回荒废了两周的读书计划，新书新开始。下周10个小时加油！争取运动看书双达标。\n\n不要在乎别人怎么看你，你的好与不好都是一样，没人在乎。\n","title":"Weekly - 20.09","time":"2020.03.01"},{"id":"9","content":"一周的时间读完了The hard thing about hard things，作者Ben Horowitz现在是Andreessen Horowitz VC的创始人，之前他和Marc Andreessen创业建立过LundCloud, Opsware 等公司。Opsware最后被HP以1.65B$收购。\n\n本书可以说是他讲述CEO的一些经验，可能遇到的一些问题，以及解决方法。\n\n如果我是一个CEO，我正在创业，这本书可以对我更有作用一些。但是即使设想自己是一个Manager，或者以此思考与理解High Level，也是有一点收获的吧。内容有一些松散，这次读后感也还是按照章节标题拆写想法吧。\n\n--\n\n<i>1. From communist to venture capitalist</i>\n\n这一章讲述了Ben Horowitz创业前经历，从童年的事情，认识妻子，到起初工作的经历。比较有意思的是Ben加入Netscape的时候27岁，29岁的时候就已经有三个孩子了，32岁创业做LundCloud。\n\n<i>2. \"I will survive\"</i>\n\nLundCloud的创业经历，如何快速增长到500人，然后经历了2000年的dotcom crash之后如何在生死边缘摸索，最后强行上市自救。最后拆除Opsware software成为了新公司，Lundcloud卖给了EDS (16.5M$)。\n\n<q>If you are going to eat shit, don't nibble\n  What would you do if we went bankrupt\n  Past the deadline is better than not having one\n</q>\n\n<i>3. This time with feeling</i>\n\n讲Opsware创业的故事，如何从一开始市场极其看衰（60M$ 现金 + 20M$ Yearly income from EDS，却估值30M$)。团结所有人，遇到EDS危机60天保住了90%的营收，\b为此收购了Telegram, 为其原来CTO支付cancer治疗费（并不需要）。之后快速增长，最后1.65被HP收购。\n\n<q>Whenever a large organization attempts to do anything, it always comes down to a single person who can delay the entire project\n  We have one bullet left in the gun and we must hit the target.\n  It was a great experience for them to be thrown into the middle of the ocean and told, \"okay, swim\"\n  Figure out the right product is the innovator's job, not the customer's job. The customer only knows what she thinks she wants based on her experience with the current product.\n  Conventional wisdom had nothing to do with the truth and the efficient market hypothesis was deceptive.\n  It's a good question to ask, \"What am I not doing\".\n</q>\n\n<i>4. When things fall apart</i>\n\n  The struggle\n  CEOs should tell it like it is\n  The right way to lay people off\n  Preparing to fire an executive\n  Demoting a loyal friend\n  Lies that losers tell\n  Lead bullets\n  Nobody cares\n\n<q>The ability to focus and make the best move when there are no good moves\n  It's the moment where you fell most like hiding or dying that ou can make the biggest difference as a CEO.\n  Keep death in mind at all times.\n  Don't be optimistic.\n  Humans particularly those who build things, only listen to the leading indicators of good news.\n  When things go wrong in your company, nobody cares.\n</q>\n\n<i>5. Take care of the people, the products, and the profits: in that order :</i>\n\n  A good place to work\n  Why startups should train their people\n  Is it okay to hire people from your friend's company\n  Why it's hard to bring big company execs into little companies\n  Hiring executives: if you've never done the job, how do you hire somebody good?\n  When employees misinterpret managers\n  Management debt\n  Management quality assurance\n\n<q>Hire for strength, not lack of weakness\n  Job of a big company executive is very different from the job of a small company executive.\n  The very best way to know what you want is to act in the role. Not just in title, but in real action.\n</q>\n\n<i>6. Concerning the going concern :</i>\n  How to minimize politics in your company\n  The right kind of ambition\n  Titles and promotions\n  When smart people are bad employees\n  Old people\n  One-on-one\n  Programming your culture\n  Taking the mystery out of scaling a company\n  The scale anticipation fallacy\n\n<q>Sometimes an organization doesn't need solution; it just needs clarity\n  Peter Principle: Sooner or Later they are promoted to a position at which they are no longer competent, and there they remain unable to ear further promotions\n  Law of Crappy People: For any title level in a large organization, the talent on that level will eventually converge to the crappiest person with the title.\n  If you like structured agendas, then the employee should set the agenda.\n</q>\n\nHow to lead even when you don't know where you are going :\n  The most difficult CEO skill\n  The fine line between fear and courage\n  Ones and twos\n  Follow the leader\n  Peacetime CEO/wartime CEO\n  Making yourself a CEO\n  How to evaluate CEOs\n\n<q>By far the most difficult skill I learned as CEO was the ability to manage my own psychology.\n  If you don't like choosing between horrible and cataclysmic, don't become CEO.\n</q>\n\nFirst rule of entrepreneurship: there are no rules :\n  Solving the accountability vs. creativity paradox\n  The freaky Friday management technique\n  Staying great\n  Should you sell your company?\n\n\n","title":"Books - The Hard Thing About Hard Things","time":"2020.02.29"},{"id":"8","content":"Reading - 3/10\nWorkout - 3.5/5\nWeight - 74.2\n\n这周完全没有完成目标，虽然周一就读完了The hard thing about hard things，但是读后感一直没写完。运动也只运动了3.5天。\n\n这周和宝宝回到了公园五号，起初几天还是比较自律的，周一二三都完成了阅读和运动。但是后面几天就几乎没有效率运动看书了，尤其是周六周日，原本应该是运动读书最好的两天，一点书没看，一点运动没做。\n\n其实这个让我想起来了去年一年在公园五号的生活。无论是读书，运动还是加班也好，不能有效的督促自己做这些事情不是这次回来的事情，其实去年一年都没做到。原因也是大致相同的吧，我觉得可以一起总结吧。看看之后怎么进步。\n\n<i>看电视</i>\n\n这周和宝宝追了想见你，追剧确实是一件很花时间的事情。原本周二周三周四还好，每天只看两集，但到了周五周六忽然就停不住自己，一口气把剩下20集全看了=。=... 所以一个周五晚上和一个周六就没了。\n\n我觉得偶尔遇到一个好看的剧也挺好哈，当然不能每周都这样看电视剧，但是偶尔看一看可以的。当然，下次看电视剧最好还是有克制的每天只看一两集，这样比较有持续性。也不会造成这周集中没有看书运动，后面几周又需要专门克制不看电视剧。不过这个确实很难吧。\n\n另外回来和宝宝每天吃饭也都会看综艺，这个也需要克制每天只能看一集一个小时。\n\n<i>空间</i>\n\n过去三周在爸妈家里一直比较高效，回来之后对比发现了一些在家里的优势。最重要的原因是爸妈家里空间比较大... 我原本不觉得这个很重要，也一直觉得我们住一个开间足够了，但是这次回来发现以后还是需要大一些的家。\n\n空间给我看书运动带来的最大困扰大约是没有私密性。看书运动是一个需要持续集中注意力的事情，这个过程中如果我可以确保不受到干扰，我的动力也会足一些。但在一个开间里面，我觉得更容易受到干扰，这也会造成更难开始做这些事。这不是怪宝宝会真的打扰我，只是这个空间不够大的原因吧。写这段话我觉得我其实有点矛盾，我觉得如果家里没有人，我可能自律起来也有些难，但是我又需要一些自己的空间。\n\n除此以外，因为现在不太能出门，也让我发现只在这40平米的空间生活，也会让我有点压抑，感觉哪怕思考一些事情来回走路的空间都不太够。\n\n写到这里我其实有些后悔去年开始的时候没走选择租新城国际，更大的空间和更近的上班距离都是太重要的事情，\b这一点我现在才发现。也是以后再租房子要提醒自己的。\n\n<i>睡眠</i>\n\n回到公园五号，发现现在早上容易睡不醒。经常睡到10点还觉得困得不行，不知道为什么每天都要睡9个小时以上。导致早上的时间总是很紧张。我觉得之后哪怕要强行喝咖啡，也要让自己早上早点起。\n\n我总结过原因是不是空间太小，空气流通不顺畅造成的，也许是？那我们也应该每天起床多开一点窗户，不能又每天越睡越晚，早上又起不来了。\n\n下周，每天9点起床，太困就喝咖啡或者下楼走走，必须做到。\n\n--\n\n和宝宝一起不出门生活一周，让我发现这件事其实比想象的难一些，见面时间更多，空间更小，尤其涉及到每天还要做饭等等，都挺不容易的。我也知道这个时候我不能像以前那样浑浑噩噩过周末一样过了。要有作息，有坚持，不沉迷。\n\n下一周，要坚持达到目标！\n","title":"Weekly - 20.08","time":"2020.02.23"},{"id":"7","content":"Reading - 8.2/10\nWorkout - 7/5\nWeight - 72.9\n\n这周阅读完成了8个小时，和上周相似，周中每天一个小时（除了周三，大约加班了？），\b\b周末每天2个小时。上周几乎第一次达标之后，本来以为这周会再接激，第一次完成10小时阅读目标。但是相反，这周比上周少了一个小时。\n\n一来，经过这几周看书的总结，看完一年看完15本身并不难，按照现在的速度基本上两周可以看完一本书了，一年30本可能没什问题了。所以也没觉得没必要强要求每周10个小时，工作起来事情还是很多的。\n二来，The Hard things about hard things比较一般吧，讲的都是怎么做CEO，有一些鸡汤。可能如果我当着CEO再看会有感触一些吧。\n\n这周运动超额的完成了7天，本来打算周五休息一天，但最后每天都做了。可能是发现在WFH的过程中，每天到下午5点有些疲惫，运动过程中分泌的多巴胺就好像兴奋剂一样，让我可以支撑晚上的看书，下象棋和poker。\b\b\b感觉尤其在一整天都不出门的日子了，这样还挺好的。\n\n从这周开始打算业记录一下体重，毕竟整年有70kg的目标，其实是不是每周都做到5/7就能达到这个目标我也不知道（+正常的饮食），说实话我也不知道是不是真的想达到这个目标，一方面我当然是不想有赘肉的，但另一方面我也想有肌肉。如果运动增加了肌肉，达不到70kg我也不是很care，只是这样不好measure？所以先记录着体重吧\n\n--\n\n又是WFH的一周，和上周相比，好的地方是我不再用为landing的事情纠结了。所以可以更好地focus在工作上了。确实这周工作效率比上周好很多了。虽然当然还是不及正常去办公室工作，但作为了一个朝九晚五来说，我觉得效率还okay了。有的时候还是会为自己的小划水有些愧疚，但是相比之下，可以更好地Workout + 看书还是更让我欣慰一些。\n\n后面两周要和小宝宝回公园五号WFH了，但愿可以保持好的工作运动看书节奏:)\n\n--\n\n这两周在家里最大的娱乐活动就是和爸爸下象棋以及和\b砍柴兄弟们玩poker了，考虑到后面两周就不太能玩到了，我想花了篇幅聊聊这两个活动哈哈\n\n<strong>象棋</strong>\n\n这个假期在家和爸爸下了很多棋，说实话自己也进步了不少，现在是真正和爸爸势均力敌了。象棋下多了会发现这里面的变化其实并没有想象得多。很多套路玩多了都有些相似，不过每次下棋还是变化出了不少一样的精彩。\n\n如果\b那我跟我爸爸比较，我觉得现在布局能力已经不差甚至强一点点了。一般来说早期和中期可以有一点优势。但是还是有两点比爸爸差一些。\n\n一是，走到最后的细棋能力，经常有微弱优势但是走到最后却还是被逼平了。这个可能还是因为经验少了，习惯早起布局的时候步步为营，忽然开朗之后可以慢慢反而非常不习惯。\n\n二是逆局抗压的能力，我容易在逆势局扛不住，容易进入一种殊死一搏，\b要死早点死的冲锋陷阵中，往往效果不好。\n\n对于二来说，这可能不仅仅是我下棋的问题，而是我人生的一个弱点。从小到大，大部分时候都太顺利，外加非常要强。容易在成我在逆境的时候沉不住气，觉得早点与死亡博殊死一搏可以让自己解脱的快一些。另外在逆境的时候我的情绪也非常不稳定，下棋的时候在逆势的时候我非常容易急躁易怒，有时候爸爸稍微笑一下我都有点接受不了。还是那个问题，太要强。\b\n\n接受别人赢过自己，接受其他人的成长，为别人喝彩，是我可能终究需要成长的地方。\n\n<strong>德州扑克</strong>\n\n玩的越多，越觉得德扑是一个很好的游戏。\b玩的过程中可以训练对于收益和风险的理性思考，也有可以折射出一个人在面对各种极端情况的感性\b人格。说这是一个迷你小人生我觉得都不算夸张。我想稍微多讲讲关于各种情况下我感性的一面\n\n1. 关于目标\n\n玩德普的目标是什么，对于我来讲当然还是输赢。什么是输赢呢？我觉得从第一句我的输赢就很明确，赢钱就是赢，输钱就是输。我不需要做赢得最多的，但是我不能是输钱的。我觉得这个目标本身没什么问题吧，\b即使有的局输了，有的局赢了，整体输赢也是可以衡量的。\n\n2. 赢钱后的策略\n\n基于上面的全局目标，当我已经赢钱的时候，我就会保守很多，赢1000和赢200对我来讲差的不多。当我在水上之后，我对于下注会保守很多。\b一般来讲如果不是很好的牌，我尽量不会让自己有下注到掉到水下的风险。往往这么玩并不是保险的事情，很多时候会被磨掉不少钱，最后一不小心掉到水下。\n\n3.输钱后的策略\n\n相反的时候，当我已经输钱了，我的要强心和不耐逆的特性就会展现。输200和输1000对我来讲也差不多。所以这个时候我会疯狂的下注，打的很激进。很多不应该玩的牌也要玩（所谓的range很大）。\n\n从水下往上爬的过程往往很辛苦，往往来讲有两个结果。\b\n\n一半情况我能爬上来，不得不说我挣扎起来真的有时候可以很厉害，打的很有气势，往往这种时候我可以打出逆风翻盘，打到水上。\n一半情况当然就是我输的很惨（就像今天）。有的时候牌就是不好，有的时候状态不是最佳，这种时候越打我会越焦虑，越焦虑我会越容易犯错。最后越输越多。\n\n认真讲，输钱从水下往上爬特别特别耗费我精神。往往无论是打没打上来，我最后都非常疲惫。这种疲惫往往也是自己要强不服输造成的。如果心态好一些，或许更多时候可以沉着一些，也不那么累一些。\n\n--\n\n所以无论是象棋也好，德扑也好，我需要的磨砺都是在如果在逆风之下沉着冷静。这是一种很好的锻炼吧，我的人生so far总是很顺利，但相信总会遇到风雨，总是赢不是一件好事，输赢起伏，输得起，才能之后更好的赢。\n\n\n\n\n\n","title":"Weekly - 20.07","time":"2020.02.16"},{"id":"6","content":"一周时间看完了To Kill a Mockingbird，Kindle真是读书神器。\n\n--\n\n故事讲述的\b是1930年代美国Alabama州一个叫Maycomb镇子（我查了一下应该编造的）发生的事情。主人公Scout是一个6-9岁的小女孩。故事的主线有两条。\n\n<i>一条是关于racist和勇气</i>\n\n主人公的父亲Atticus是一个律师，他被委任成为黑人Tom Robinson的律师，Tom被起诉强奸了镇子上的白人姑娘Mayella Ewell。虽然案件中的证据都明显指明Tom是被诬陷的，（真相是Mayella诱惑Tom被父亲当场抓获，然后遭到殴打污蔑Tom强奸）。但最终法庭还是宣判了Tom的guilty，最后Tom在脱狱的时候被射杀。\n\n“Yes, suh. I felt right sorry for her, she seemed to try more’n the rest of ‘em—”\n“You felt sorry for her, you felt sorry for he?”\n\n法院上，当Tom说他帮助Mayella是因为他sorry她的时候，氛围上的改变简直是精妙，一场源于种族歧视的悲剧也注定了发生，无论证据如何明显。\n\n“I think there’s just one kind of folks. Folks.”\n\n“I wanted you to see what real courage is, instead of getting the idea that courage is a man with a gun in his hand. It’s when you know you’re licked before you begin but you begin anyway and you see it through no matter what. You rarely win, but sometimes you do.”\n\n<i>一条是关于偏见和善良</i>\n\nBoo Radley是家访邻居，传闻Boo以前是个很恶劣的人，但是已经很久没人见过他了，他从不出门。Scout和Jem对他充满好奇，随着故事的进展他们从原先无比害怕他，到他 leaves Jem and Scout gifts之后又帮助mends Jem’s pants，他们对他更多是好奇和想见上一面。故事的最后，当Bob Ewell 想要杀害Jem and Scout的危难时候，Boo 终于出现救了他们的性命。\n\n“Atticus, he was real nice.” “Most people are, Scout, when you finally see them.”\n\n\b--\n\n刚看完的一瞬间我都没有想明白为什么这本书叫To Kill a Mockingbird，看了一些网评才想明白很多细节，Mockingbird寓意的是善良。\b\b\n\n当Atticus和Tate发现是Boo Radley杀死Bob救了Scout和Jem之后，\n\n\"Mr. Finch, taking the one man who's done you and this town a great service and draggin' him with his shy ways into the limelight ---to me that's a sin.....If it was any other man it'd be different.  But not this man, Mr. Finch.\"\n\nTate想到Boo不希望因为收到更多关注后，就决定判断Bob Ewell是以外杀死了自己。Atticus想通这件事之后就问Scout是否明白了其中缘由。Scout说\n\n\"Well, it'd be sort of like shootin' a mockingbird, wouldn't it?\"\n\n--\n\n嗯，不要辜负善良\n","title":"Books - To Kill a Mockingbird","time":"2020.02.10"},{"id":"5","content":"Reading - 9.3/10\nWorkout - 5/5\n\n第一周wfh，最大的感受大约就是工作没效率\n\n9:00 - 9:30：起床吃点东西准备上班（认真讲，不用上下班commute的时间还是很爽的），穿插着读读书\n17:00: 下班，健身到18:00\n19:00: 吃完晚饭，下棋/德扑 + 读书\n\n没效率主要几个原因：\n1. 在家里缺少peer pressure让我有动力干活\n2. 身上的任务比较缥缈，没有deadline，需要self-driven。在这个时候就很难motivate自己，写文档也是我的弱项，本来好不容易写了一个文档，又觉得不理想又重新推翻重写\n3. 等待和discuss landing的offer\b\b\b占据了很多精力，无心工作\n\n这周意几乎完成了10个小时的阅读任务，自己都有点意外。《杀死一只知更鸟》本身其实并没有那么好看，但可能kindle体验确实很好。周中每天看一个小时，（了周二因为等offer睡得太少），周末读2-3个小时，希望下周可以保持。除此以外也完成了5次运动。下周接着保持运动3，4天休息一天的节奏。\n\n--\n\n这周拿到landing offer，Naixin也接了global offer，算是解决这个让我们纠结很久的选择。虽然嘴上说回美国回美国，但我想我们都没有想到真的会发生的这么快吧，至少我没有，至少我们可能一个月前也没有。世事难料，计划总是赶不上变化。\n\n回想一下，这一切确实发生的太突然太快，\b做这些选择也确实都是最好的选择，只是恰好他们都是回美国的选择。\n\n加入landing是一个挑战，至少这个挑战还没有开始已经让我学习了很多东西。最近学习了很多创业公司的概念，option，stock，融资有关的信息。想想回国前还想过创业，真的是有点naive吧。\n\n有的时候我真的不得不去相信命运，相信我在被命运眷顾。在最及时的时候，总会出现一个理想的机会让我去把握。嘴上说着这都是我的努力成就的，是因为自己优秀才有这些机会，但是心底里却实实在在敬畏着这份命运的安排。人越是长大，越是明白自己的渺小，社会，命运，在这些力量面前我们无所适从，只能顺流。\n\n--\n\n\b说一下这周生气的事情，这周生气了两次。如果说这两件事有什么收获，那就是意识到虽然我学习了NVC，但当被情绪控制的时候，都是无法发挥他的效果，在想真正用好NVC之前，先要学会处理自己的情绪（也或许处理自己的情绪就是NVC）\n\n<i>第一次</i>，实在讲是我的问题，我没有任何道理。我因为等待结果太焦虑，结果无缘无故的发泄在了naixin身上。我都没有脸面去承认，这其实跟我是不是想早睡，你是不是早早跟我语音了一点关系没有，只是我太焦虑，任何不顺心的事情都会像扎在气球上的针，让它爆炸。\n\n焦虑是我担心最后没有拿到landing offer。担心没拿到是因为我投入了精力准备，告诉了你和爸妈，我的自尊心压在了这上面。我害怕失败，\b害怕因为我要为你没法去美国负责，或者我害怕你就去了我一个人留下了。害怕是我的虚荣心作祟，承担不起一次大家都在期待的事情的失败，我接受不了。\n\n如果是这样，怎么消除它了。我也没想好，可能是告诉我如果没拿到我们就留下（你好像讲了？）。可能是告诉我这本身就是一个机缘巧合的机会，失去了我们还是回到一个月前，一切都没有损失。\b可能是告诉我不要那么强虚荣心，你最亲近的人才是不会因为一次失败看不起你的人，你可以暴露你的脆弱和局限。\n\n<i>第二次</i>，因为你再次推迟你回来的时间，我生气了。\b关于这件事，我反而更能理解我自己，我不认为我的需求有问题，我也不认为我的观点有问题。\n\n如果对待生死风险的\b衡量不一致本来就是我们很大的不同，那这件事就是价值观不同本人。你认为回来有危险，我认为没危险。你认为晚一周回来更安全，其实我不知道，但我已经不满你不愿承担回来的风险了，所以你晚点回来是不是更安全，可能本身不重要了。\n\n另一方面，\b\b\b我内心现在有一个对你爸爸妈妈的偏见，我偏见的认为他们不喜欢我，认为他们没有在祝福我们。就好像我们在语音时候你妈妈叫你，我会想她不知道我们在打电话么，这么打扰考虑我的感受么。所以他们在不希望你早回来这件事上也不考虑我们想早点彼此相见么？有一点点考虑到你还有男朋友在北京等你回来么？\n\n我知道这个偏见可能不对也不好，但这个需求的空虚也确实存在，没法简单自愈。\n\n所以我不认为自己的需求有错，我也不认为自己的判断（至少在风险这件事上）有错。那我哪里可以做的更好呢？我不应该让情绪掌控我的需求。\n\n读To Kill A mockingbird 里，主角的父亲是一个让人着迷的人。Atticus 对很多事情有自己的看法，也有很多自己的需求，但是他表达自己的方式总是很温文尔雅，好像没有什么可以让他暴跳如雷，它可以谦逊的表达自己所有的观点。\n\n这或许是我欠缺的吧，可以有需求，有不一样的想法，但始终沉着冷静，always keep calm\n\n--\n\n呼吸\n\b我为什么在生气\n自己什么需求没有被满足\n需求来自何处\n\n","title":"Weekly - 20.06","time":"2020.02.09"},{"id":"4","content":"Reading - 6/10\nWorkout - 5/5\n\n第一次按时写Weekly! 以后要keep up。\n\n--\n\n上周最后还是没有达成10小时读书计划，准备面试的两次几乎没看，外加这次Weekly只有6天，也算是可以理解吧！这周读完了NVC，并写了读后感，我觉得还是不错的，写一遍对于加深印象还是很有帮助的。希望以后都可以做到。\n\n在选择下一本这件事让我头疼了一小下。慢慢发现选择一本读的下去的书还是一件挺难的事情。最后选择了《杀死一只知更鸟》，算是读个故事让自己放松一下吧。哦对了，我还是买了kindle，so far还不错，一开始确实不太适应，有点像个古董，但是用顺了觉得还不错。最大的优点是能选择自动翻译，还挺有帮助的，尤其感觉后面一段时间都大部分会读英文书。\n\n--\n\n这种6天运动了5天，完成这个achievement也是指的嘉奖的，下周加油！\n\n--\n\n面完landing了，虽然不想太自信，但是感觉整体问题应该不大吧。上次应何总邀请去讲前端面试似乎起到了不小作用，所有面试官（除了Andrew本人）都是让天在场的，明显感觉他们都对我很nice，他们也说面试题目都是被我启发的，都让我面的压力很大哈哈。几个面试中都有一些point觉得我做的不够好，但是整体还是差强人意吧。\n\n下面就是等周二出结果了，焦虑的感觉明后天都没办法明认真上班了lol。不过反正也是wfh，水一点就水一点吧。\n\n--\n\n武汉病毒猖獗的厉害，这一周都是在家度过的，除了去了两次圣熙八号+几次去楼下取京东快递。剩余时间都没出门。这几天的Routine也比较固定，读书，下棋，运动，看电影，准备面试。虽然还是没有达到每周阅读的10h要求，但是我还是对自己比较满意了。高中以后在家的时间我都是颓废过去的，这周大约已经是2010年以来在家效率最充实的一段时间了，至少没有玩游戏:)\n\n这一周（其实也包括上周）也是很久以来和爸爸妈妈相处最多的一段时间，我挺开心的，我想他们也挺开心的。有一天爸爸说这样的在一起的机会以后也不多了，忽然有点小伤感。趁着现在可以，就多陪陪他们吧。\n\n\n","title":"Weekly - 20.05","time":"2020.02.02"},{"id":"3","content":"<strong>-- 读后感 --</strong>\n\nNVC核心的观念在于沟通重在双方的感受和需求，表达自己的感受和需求，同情别人的感受和需求。\n\n1. 挖掘自己的感受和需求是什么，尤其是生气的时候，明白不是别人让我生气，是自己的需求没有被满足。\n2. 表达的时候按照NVC，去表达自己的感受和需求。\n3. 聆听别人的时候不做judgments，empathize对方的感受。\n\n<strong>-- 每章摘要 --</strong>\n\n<i>1. Giving From the Heart</i>\n\n<q>NVC: a way of communicating that leads us to give from the heart.\n  We perceive relationships in a new light when we use NVC to hear our own deeper needs and those of others.\n  Four components of NVC: 1. observations 2. feelings 3. needs 4.requests.\n  Two part if NVC: 1. express honestly through the four components. 2. receive empathically through the four\n  components.</q>\nNVC的核心理念是沟通应该focus on 人的feelings and needs。世界上所有人的feel 和 needs是共同的。如此沟通能解决所有障碍。\nNVC四要素1. observations 2. feelings 3. needs 4.requests。在自我表达的时候honest表达四要素\b，聆听别人的时候利用empathy发现别人的feelings and needs.\n\n<i>2. Communication That Blocks Compassion</i>\n\n<q>Certain ways of communicating alienate us from natural state of compassion.\n  In the words of judgments, our concern centers on \"who is what\".\n  Analyses of others are actually expressions of our own needs and values.\n  Classifying and judging people promotes violence.\n  Comparisons are a form of judgment.\n  Our language obscures awareness of personal responsibility.\n  We can replace languages that implies lack of choice with language that acknowledges choice.\n  We are dangerous when we are not conscious of our responsibility for how we behave, think and feel.\n  We can never make people do anything.\n  Thinking based on \"who deserves what\" blocks compassionate communication.\n  Life-alienating communication has deep philosophical and political roots.</q>\n沟通时候杜绝做judgment，不做比较，才可以有compassion.\n\n<i>3. Observing Without Evaluating</i>\n\n<q>When we combine observation with evaluation, people are apt to hear criticism.</q>\nobserve的时候做到客观，不做分析\n\n<i>4. Identifying and Expressing Feelings</i>\n\n<q>Expressing our vulnerability can help resolve conflicts.\n  Distinguish feelings from thoughts.\n  Distinguish between what we feel and what we think we are.\n  Distinguish between what we feel and how we think others react or behave towards us.</q>\n挖掘真是内在的感受，不带任何思考，不受别人影响\n\n<i>5. Taking Responsibility for Our Feelings</i>\n\n<q>What others do may be the stimulus of our feelings, but not the cause.\n  Four options for receiving negative messages: 1. blame ourselves. 2 blame others. 3. sense our own feelings and needs.\n  4. sense others' feelings and needs.\n  Connect your feeling with your needs: \"I feel ... because I need ...\".\n  Distinguish between giving from the heart and being motivated by guilt.\n  Judgments of others are alienated expressions of our own unmet needs.\n  If we express our needs, we have a better chance of getting them met.\n  If we don't value our needs, others mat not either.</q>\n感受是自己的，别人可能刺激，但是不会产生你的感受。从自己的感受出发发觉自己的需求，不要从愧疚中。不对别人做judgments或者criticism，重视自己的感受（或者别人的）。\n\n<i>6. Requesting That Which Would Enrich Life</i>\n\n<q>Use positive language when making request.\n  Making requests in clear, positive, concrete action language reveals what we really want.\n  Vague language contributes to internal confusion.\n  Depression is the reward we get for being \"good\".\n  Request might sounds like demand when unaccompanied by the speaker's feeling and needs.\n  To make sure the message we sent is the message that's received, ask the listener to reflect it back.\n  After we express ourselves vulnerably, we often want to know 1) what the listener is feeling, 2) what the listener is\n  thinking. 3) whether the listener would be willing to take a particular action.\n  In a group, much time is wasted when speakers aren't certain what response they're wanting.\n  Demand vs Request: demand can be only submitted or rebelled.\n  Our objective is a relationship based on honesty and empathy.\n</q>\n了解Needs之后是一个明确的request，一定要明确避免一切模糊。最好让listener重复一遍。在给一个group\ncommunicate的时候一定要明确我们想到得到什么。request不是demand，不是只有做或者不做，不做批判或者judge，不造成guilt，如果对方不同意，用empathy去理解对方对方的needs.\n\n<i>7. Receiving Empathically</i>\n\n<q>Empty our mind and listening with our whole being.\n  Ask before offering advice or reassurance.\n  Intellectual understanding blocks empathy.\n  Listen to what people are needing rather than what they are thinking.\n  When ask for information, first express our own feelings and needs.\n  Paraphrase only when it contributes to greater compassion and understanding.\n  Paraphrasing saves time.\n</q>\n作为听着，不要着急去说自己的建议或者想法，挖掘倾诉者的feelings and needs，只听。给建议之前先询问。\n\n<i>8. The Power of Empathy</i>\n\n<q>\"Don't just do something...\"\n  It's harder to empathize with those who appear to possess more power, status or resources.\n  The more empathize with the other party, the safer we feel.\n  Rather than put your \"but\" in the face of an angry person, empathize.\n  It may be difficult to empathize with those who are closest to us.\n  That bores the listener bores the speaker as well.\n  Speakers prefer that listeners interrupt rather than pretend to listen.\n  Empathize with silence by listening for the feelings and needs behind it.\n</q>\n如何更好地去Empathize？还是去抓住对方的感受，这对亲人和上级比较难一些。另外适当的interrupt去empathize对方的感受，甚至可以适当的沉默。\n\n<i>9.Connecting Compassionately With Ourselves</i>\n\n<q>NVC's most important use may be developing self-compassion\n  We use NVC to evaluate ourselves in ways that engender growth rather than self-hatred.\n  Avoid shoulding yourself!\n  Self-judgments, like all judgments, are tragic expressions of unmet needs.\n  NVC mourning: connecting with the feelings and unmet needs stimulated by past actions we now regret.\n  NVC self-forgiveness: connecting with the need we were trying to meet when we took the action that we now regret.\n  We are compassionate with ourselves when we are able to embrace all parts of ourselves and recognize the needs and\n  values expressed by each part.\n  We want to take action out of the desire to contribute to life rather than out of fear, guilt, shame, or obligation.\n  With every choice you make, be conscious of what need it serves.\n  Be conscious of actions motivated by the desire for money or approval, and by fear, shame, or guilt. Know the price\n  you pay for them.\n  The most dangerous of all behaviors may consist of doing things \"because we're supposed to\".\n</q>\n本书最重要（自认为）是对自己使用NVC，要永远明白自己的做的事情在serve自己的什么need。\n不要因为fear, guilt, shame, or obligation做事情，如果做了，也要明白这里面的代价。\n不要说自己”应该“做什么，应该是外部施加的，而应该从自己的内在需求，说”需要“什么所以要做什么。\n如果regret做了什么事情，去思考当时有什么need没有被满足，或者我们当时在尝试满足什么need，而是行为不对。\n\n<i>10.Expressing Anger Fully</i>\n\n<q>Hurting people is superficial.\n  We are never angry because of what others say or do.\n  To motivate by guilt, mix up stimulus and cause.\n  The cause of anger lies in our thinking - in thoughts of blame and judgment.\n  When we judge others, we contribute to violence.\n  Use anger as a wake-up call.\n  When we become aware of our needs, anger gives away to life-serving feelings.\n  Violence comes from the belief that other people cause our pain and therefore deserve punishment.\n  Judgments of others contributes to self-fulfilling prophecies.\n  Steps to expressing anger: 1. Stop.Breathe. 2. Identify our judgmental thoughts. 3. Connect with our needs. 4. Express\n  our feelings and unmet needs.\n  Stay conscious of the violent thoughts that arise in our minds, without judging them.\n  Our need is for the other person to truly hear our pain.\n  People do not hear your pain when they believe they are at fault.\n  Take your time.\n</q>\n本书对我最重要的一章，如何处理Anger。\n我们anger不是因为别人做了什么，而且是源自自己的needs。\n因为anger去伤害别人是肤浅的，没办法帮助我们实现自己的need。\n当生气的时候。1. 深呼吸 2. 把握生气时候的想法 3. 挖掘自己的需求是什么。4. 通过NVC表达自己的需求。\n慢慢来。\n\n<i>11. Conflict Resolution and Mediation</i>\n\n<q>Creating a connection between people is the most important thing.\n  When you make the connection, the problem usually solves itself.\n  Avoid the use of language that implies wrongness.\n  Learn to hear needs regardless of how people express them.\n  Criticism and diagnosis get in the way of peaceful resolution of conflict.\n  People often need empathy before they are able to hear what is being said.\n  The objective is not to get the other parties to do what we want them to do.\n  Role-play is simply putting ourselves in the other person's shoes.\n</q>\n处理conflict的时候最重要的是让双方可以交流彼此的needs。 当双方能听到对方的needs问题就自然会解决。如果有一方有太多pain我们就empathy，听到no不是rejection，是一个机会我们我需要进一步了解needs.\n\n<i>12. The Protective Use of Force</i>\n\n<q>The intention behind the protective use of force is only to protect, not to punish, blame or condemn.\n  Fear of corporal punishment obscures children's awareness of the compassion underlying their parents' demands.\n  When we fear punishment, we focus on consequences, not on our own values.\n  Fear of punishment diminishes self-esteem and goodwill.\n</q>\n\n使用force的主要intention只能是protect，punish会产生敌意和抵触心理，即使可以实现你希望别人做什么的结果，但是别人的motivation还是很低，无法长期维持。\n\n<i>13. Liberate Ourselves and Counseling Others</i>\n\n<q>The ability to hear our own feelings and needs and empathize with them can free us from depression.\n  Focus on what we want to do rather than what went wrong.\n  Defuse stress by hearing our own feelings and needs.\n  Defuse stress by empathizing with others.\n  I empathized with clients instead of interpreting them; I reveled myself instead of diagnosing them.\n</q>\n\nNVC帮助inner communication，当我们有负面情绪的时候，把他们转换成feelings and needs然后去empathy. 发现自己深处的需求而不是怪罪自己或者别人.\n\n<i>14. Expressing Appreciation in NVC</i>\n\n<q>Compliments are often judgments - however positive-of others.\n  Express appreciation to celebrate, not to manipulate.\n  Saying \"thank you\" in NVC: \"this is what you did; this is what I feel; this is the need of mine that was met.\"\n  Receiving appreciation without feeling of superiority or false humility.\n</q>\n表达appreciation也会从NVC做起，表达对方做了什么，你的感受如何，你的什么need被fulfill了。不要试图通过appreciation去操控别人，长期无效。\n","title":"Books - Nonviolent Communication","time":"2020.02.01"},{"id":"2","content":"Reading - 4.8/10\nWorkout - 1/7\n\n今天周一，上个周记是周三写的，所以这次周记只记录了5天，1天在南昌，1天在泾县，1天在路上，2天在路上。\n\n作为了一个短暂的5天周记，完成了一半的读书计划还是不错的。但实际上都是最近三天很努力的在读书，前面两天并没有读书。最近都是假期，理论上我没有什么理由（太忙）不去读书。所以假期前几天（尤其在南昌的时间？）还是没有好好利用起来读书吧。\n\n运动方面，在寒冷的南昌和泾县是很难昨天运动了。今天回到北京家里完成了做一组胸肌+HIT，有点累。每天做两个训练的组合算作一天运动。我要把运动降到一周5次了。\n\n--\n\n武汉肺炎愈发的严重了，看着每天上涨的数字都觉得吓人。不过对我个人经历来说，这次流感倒不完全是坏事吧（当然，这里面有个忙碌乐观的前提，那就是我相信自己不会中招:D）。\n\n一来，这个流感让我有更多的机会和爸妈在一起，从去厦门初七回，到在泾县初四回，到最后临时初二就回到北京了，我们一起做了不少决定。现在一家人困在北京家里不出门，我能感觉到其实爸爸妈妈还是比较高兴能和我多待在一起一些时间的。\n\n二来，这个流感也打乱了本来旅游的机会，外加流感延迟了上班的时间并且给了额外的wfh的时间，\b我又更多'宅'的时间做例如看书，准备面试，运动，学习，陪爸妈等等事情。至少北京的第一天过得很充实，希望后面的一周都能如此吧。\n\n--\n\n读Non Violent Communication，终于读到了我觉得比较有用的一部分：如何在自己身上使用NVC。\b前面讲的很多NVC实用性并不高，因为现实中非常难真的去实践一些理论，像一个复读机一样去重复别人的话也太傻了点吧！\n\n不过把NVC 落实在自己身上，是一个很practical也对我很必要的事情。我觉得自己很需要NVC去学会如何控住自己的生气，同时不去被敏感的shame, guilt去操控行为，多从自己内心的needs出发，筛选真正想做的事情并给与足够的动力。我现在就能想到过去一个月一些或大或小的事情我可以用来分析解析帮助转变为对自己needs的认知。\n\n具体的总结在写读书感的时候再写吧\n\n--\n\n说说这一周的期待吧，首先，这一周我要完成每周10h的读书计划！如果这周都完成不了，我没理由去相信我以后也能做到。这周要之前运动五次！然后好好准备面试，每天和爸爸下棋。每天的计划要大致如下\n\n上午：9点起床，做题/学习/coursera\n下午：看书，下棋，运动\n晚上：看书，做题/学习/coursera，打电话，1点之前睡觉。\n\nAnyways差不多这样的排列组合吧。\n\n\n","title":"Weekly - 20.04","time":"2020.01.27"},{"id":"1","content":"Reading - 2/10\nWorkout - 3/7\n\n对不起，周记这次又迟到了3天，上个周日补班就忘记了。现在赶上春节放假，先把上周的周记补上。\n\n读书，non violent communication 还是不错的，本来以为英文读物需要适应适应，但读起来发现做着用词还是比较常规的。反倒是一些引用别人的话总是晦涩难懂。读书目标的最大blocker还是玩游戏太多。从今天开始我要人人真真的戒fifa了。下个week一定要完成10个小时的读书计划。\n\n运动，这周去了一次健身房，office workout了两次。感觉除非天暖又开始踢球打球，现在看来每天运动的指标还是很难的。\n\n两个指标还是不达标的，我需要加油了呀。\b\n\n过去一周（算到今天），最大的事情是和landing.ai的Hr小姐姐来北京聊了聊加入的事情。原本随着上次和何总两个Eng的灵光一现一拍即合被拍死之后这件事就消停了。我以为又回到了Airbnb的正常职业发展track上来... 当然这没有什么可抱怨的，过去一年我greatly exeed 之后升了L5，成功的完成了从performance到miniapp的换组，现在也在right track上去成为这个组的EM，可以说这里的职业发展是顺风顺水。被赏识，被提拔，我觉我完全不能ask for more了。。\n\n但是当和小姐姐聊完 landing.ai 的机会，我不得不承认，这个挑战太有吸引力了。\n\n职业规划来讲，我一直相信（就像很多人一样），我终究会尝试加入一家早期的startup去面对更大的挑战。但是选择startup并且太难了，对于内心深处保守的我来说，我没办法相信大部分startup会成功。\b但是landing.ai是andrew ng创建的，我还能找到比andrew更让我信服安心的ceo么？landing.ai里面有何总，我还能找到比何总更让我愿意在一家startup工作的朋友么？？\n\n生活愿景来讲，如果我们终究要回到美国，回到湾区。landing.ai愿意就接下来的4月份抽签直接做抽2次H1b，我还能有更好的方式回到美国么？？Airbnb的L1？再次读书？\n\n一家令我信服(还是做ai的)的startup，帮助我回美国，这个诱惑，实在太大。\n\n\b然而我还是不得不犹豫，一方面是我需要考虑nx的选择，另一方面也是airbnb现在的机遇也真的很好。。我过得这个如鱼得水，我很快要成为EM，我的前景是无比美好的，Airbnb也可以让我L1回到美国，也许...在这里做着做着就不会回到美国呢？未来会怎么样，谁都说不好...\n\n道理告诉我们，人生的重要的转折，都是发生在年轻时的不刻意间。然而这个转折，大的肉眼可见。\n\n我到底在追求什么，我想要生活在哪里，又想要什么样的生活。。\n","title":"Weekly - 20.03","time":"2020.01.22"},{"id":"0","content":"Reading - 3.4/10\nWorkout - 3/7\nSnowboarding - 1\n\n\b这周读书和运动都是不达标的，加油吧。\n\n这周发生的一些事\n1. 开始去健身房，30分钟器械+30游泳的组合好像不错。这周去了2次，感觉还不错\n2. 找到了比较不错的Time tracker，希望可以一直用下去\n3. 玩的游戏还是有点多，要慢慢少玩一点\n4. 看的电视还有点多，要少看一点\n5. 读书可以多一些\n\n这周终于把《全球通史（上）》看完了，回头写个读书笔记吧。不要懒掉。\n\n好吧，万事开头难，这周就不太为难自己了，就这样吧哈哈哈哈。\n\n","title":"Weekly - 20.02","time":"2020.01.13"}]